body {
  position: relative;
}

header {
  height: $header-height;
  width: 100%;
  padding: 0 $mobile-margin;
  background-image: $teal-primary;
  color: $off-white;
  position: fixed;
  z-index: 3;

  section {
    display: flex; 
  }
 // container for logo and close button
  section:nth-child(1) {
    padding: $mobile-margin 0 0 0;
// container for logo
    .logo-container {
      flex-basis: 70%;
    }
// login button


// mobile menu close button
    div:nth-of-type(2) {
      padding: 0;
      background: none;
      border: none;
      flex-grow: 1;
      position: relative;
      //padding-right: 1rem;
      img {
        position: absolute;
        right: 3.5rem;
        top: .75rem
      }
      button {
        @include inverted-button;
        position: absolute;
        right: 3.5rem;
        top: .75rem;
        background: none;
        border: white solid 0.1rem;
        color: white;
      }
    }   
  }
  .user-details {
    display: flex;
    font-weight: bold;
    padding-top: 1rem;
    //float: right;
    p:nth-child(1) {
      padding-right: 1rem;
      border-right: 2px solid $off-white ;
    }
    p:nth-child(2) {
      padding-left: 1rem;
      //border-right: 2px solid $off-white ;
      span {
        font-weight: normal;
        text-transform: uppercase;
      }
    }
    button {
      display: none;
    }
  } 
}

.content {
  // content height = full mobile screen - combined divider height - header height
  height: calc(100vh - .3rem - #{$header-height});
  background-color: $off-white;
  display: flex;
  flex-direction: column;
  padding-top: #{$header-height};
  section {
    //turn these off to make card scroll work with titles in section. will probably remove.
    //display: flex;
    //align-items: center;
    border-bottom: $divider-grey solid .1rem;
  }
  .stats-and-polls {
    height: 150px;
    width: auto;
  }
  .surveys-quizzes {
    height: 37.5%;
    min-height: 250px;
    width: auto;
    align-items: baseline;
    //background-color: rgb(44, 116, 54);
  }
} 

.surveys-quizzes {
  h1{
      @include section-heading;
  }
}
