.status-card {
    // p {
    //     margin: 1rem 0 0 0;
    // }
    position: relative;
    @include card;
    @include small-card;
    span {
        font-weight: 500;
        font-family: $heading;
    }
    span:nth-of-type(1), span:nth-of-type(1) {
        color: $highlight;
        font-size: 1.25rem;
 
    }
    span:nth-of-type(3) {
        color: $rookie-col;
        font-size: 1.25rem;
    }
    span:nth-of-type(4) {
        color: $rookie-col;
        font-size: .75rem;
    }
    .back-progress {
        display: inline-block;
        position: relative;
        height: 6px;
        width: 150px;
        background: $off-white; 
        border-radius: 1rem; 
    }
    //text
    p:nth-of-type(1) {
        font-family: $copy;
    }
    //progress bar
    p:nth-of-type(2) {
        display: inline-block;
        margin: 0 1rem 0 1rem;
        font-size: 1rem;
    }

    #trophy-icon {
        width: 1.5rem;
        height: 2rem;
        background-image: url("/assets/rookie-trophy.svg");
        background-size: contain;
        background-repeat: no-repeat;
        position: absolute;
        bottom: 3rem;
        right: 1rem;
    }

    button {
        margin-top: .25rem;
        border: $secondary solid .1rem;
        
        @include inverted-button;
    }
    button:active {
        background: $secondary;
        color: white;
    }
}

.rookie-cols {
    span:nth-of-type(1), span:nth-of-type(2) {
        color: $rookie-col;
    }

    span:nth-of-type(3), span:nth-of-type(4) {
        color: $pro-col;
    }

    #trophy-icon {
        background-image: url("/assets/pro-trophy.svg");
    }
}

.pro-cols {
    span:nth-of-type(1), span:nth-of-type(2) {
        color: $pro-col;
    }

    span:nth-of-type(3), span:nth-of-type(4) {
        color: $champion-col;
    }

    #trophy-icon {
        background-image: url("/assets/champ-trophy.svg");
    }
}

.champ-cols {
    span:nth-of-type(1), span:nth-of-type(2) {
        color: $champion-col;
    }

    span:nth-of-type(3), span:nth-of-type(4) {
        color: $all-star-col;
    }

    #trophy-icon {
        background-image: url("/assets/all-star-trophy.svg");
    }
}

.star-cols {
    span:nth-of-type(1), span:nth-of-type(2) {
        color: $all-star-col;
    }

    span:nth-of-type(3), span:nth-of-type(4) {
        color: $all-star-col;
    }

    #trophy-icon {
        background-image: url("/assets/all-star-trophy.svg");
    }
}